.preloader {
	width:100%;
	height:100%;
	position:fixed;
	left:0;
	top:0;
	z-index:$zi6;
	overflow: hidden;
	&.preloader_siri {
		.inner {
			opacity: 0;
		  user-select: none;
		  pointer-events: none;
		  cursor: default;
			// small
			@media screen and (max-width: 39.938em) {
				opacity: 1 !important;
				pointer-events: auto;
			}
			@include transition(.2s);
			&.inner_show {
				opacity: 1 !important;
				pointer-events: auto;
				@include transition(.2s);
			}
		}
		.loader-inner{
			background: linear-gradient(237deg, $primary 37%, $secondary 100%); z-index:1;
			.bg-2 {
				background: #FDFDFD;
			}
			.headphone_hint {
				background-image: url("../images/headphone_hint.svg");
				cursor: pointer;
				width: 292px;
				height: 185px;
				@include contain;
				position: absolute;
				left: 50%;
				top: 50%;
				opacity: 1;
				transform: translate(-50%, -50%);
				animation-duration: 4s;
		    animation-timing-function: ease-out;
		    z-index: 9999;
		    @include transition(.4s);
			}
			.intro_text {
				@include title($lightest);
				font-size: 30px;
				opacity: 0;
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%);
				// small
				@media screen and (max-width: 39.938em) {
					font-size: 20px;
					line-height: 25px;
					transform: translate(0, -50%);
					left: 20px;
					right: 20px;
				}
				&.first_text{
					animation-duration: 4s;
					animation-delay: 1s;
			    animation-timing-function: ease-out;
				}
				&.second_text{
					animation-duration: 4s;
					animation-delay: 5s;
			    animation-timing-function: ease-out;
				}

			}
			#siri_wave {
				animation-delay: 9s;
				animation-duration: 4s;
		    animation-timing-function: ease-out;
				-webkit-animation-fill-mode:forwards; /*Chrome 16+, Safari 4+*/
				-moz-animation-fill-mode:forwards; /*FF 5+*/
				-o-animation-fill-mode:forwards; /*Not implemented yet*/
				-ms-animation-fill-mode:forwards; /*IE 10+*/
				animation-fill-mode:forwards; /*when the spec is finished*/
				opacity: 0;
				width: 100%;
				margin: 0 auto;
				height: 250px;
				background-repeat: no-repeat;
				background-origin: center;
			}
			.skip {
				position: fixed;
				bottom: 30px;
				left: 50%;
				transform: translate(-50%, 0%);
				cursor: pointer;
				// small
				@media screen and (max-width: 39.938em) {
					transform: translate(0%, 0%);
					left: 20px;
					right: 20px;
				}
			}
		}
	}
	.loader-inner{
		width:100%;
		height:100%;
		float:left;
		text-align:center;
		background:#FDFDFD;
		position:fixed;
		overflow:hidden;
		.bg-1{
			width:100%;
			height:100%;
			position:absolute;
			left:0;
			top:0;
			background:$primary;
			z-index:1;
			-webkit-transform: translateY(-100%);
			-moz-transform: translateY(-100%);
			transform: translateY(-100%);
		}
		.bg-2{
			width:100%;
			height:100%;
			position:absolute;
			left:0;
			top:0;
			background: linear-gradient(237deg, $primary 37%, $secondary 100%); z-index:1;
			-webkit-transform: translateY(-100%);
			-moz-transform: translateY(-100%);
			transform: translateY(-100%);
		}
	}
}

.page-loaded{
	margin:0;
	.preloader{ 
		-moz-transition: all 0.3s ease;
		-o-transition: all 0.3s ease;
		-ms-transition: all 0.3s ease;
		transition: all 0.3s ease;
		transition-delay: 0.1s;
		visibility:hidden;
		.loader-inner{
			opacity:0;
			-moz-transition: all 0.5s ease;
			-o-transition: all 0.5s ease;
			-ms-transition: all 0.5s ease;
			transition: all 0.5s ease;
			transition-delay: 1.5s;
			.bg-1{
				-moz-transition: all 0.5s ease;
				-o-transition: all 0.5s ease;
				-ms-transition: all 0.5s ease;
				transition: all 0.5s ease;
				transition-delay: .5s;
				-webkit-transform: translateY(0);
				-moz-transform: translateY(0);
				transform: translateY(0);
				transition-timing-function: cubic-bezier(.85,0,.15,1);
				-webkit-transition-timing-function: cubic-bezier(.85,0,.15,1);
			}
			.bg-2{
				-moz-transition: all 0.5s ease;
				-o-transition: all 0.5s ease;
				-ms-transition: all 0.5s ease;
				transition: all 0.5s ease;
				transition-delay: .9s;
				-webkit-transform: translateY(0);
				-moz-transform: translateY(0);
				transform: translateY(0);
			}
		}
		.inner img{
			-moz-transition: all 0.5s ease;
			-o-transition: all 0.5s ease;
			-ms-transition: all 0.5s ease;
			transition: all 0.5s ease;
			transition-delay: .2s;
			opacity:0;
		}
		.navbar-default{
			-webkit-transform: translateY(0);
			-moz-transform: translateY(0);
			transform:translateY(0);
			transition-delay: 1.5s;
		}
	}
}


/* PAGE TRANSITION */
.page-transition{ width:100%; height:100%; position:fixed; left:0; top:0; opacity:0; padding:20px; visibility:hidden; z-index:13;}
.page-transition .inner{ width:100%; height:100%; float:left; background:#FDFDFD;}
.page-transition{-moz-transition: all 0.3s ease; -o-transition: all 0.3s ease; -ms-transition: all 0.3s ease; transition: all 0.3s ease;}
.page-transition.show-me{ opacity:1; visibility:visible; padding:0;}



/* MIDDLE DIV */
.middle-div{ width:100%; height:100%; display:table; margin:0; position:relative; z-index:2;}
.middle-div .inner{ display:table-cell; vertical-align:middle;}
.parent { display: table; float:left; }
.parent > div {display: table-cell; vertical-align:middle;}

.loaderGif {
  width: 150px;
}
